// ** React Imports
import { Link } from 'react-router-dom'
// ** Store & Actions
// ** Icons Imports

// ** Reactstrap Imports
import { Badge } from 'reactstrap'
import moment from 'moment'
const repeatsvg = require('@src/assets/images/svg/repeat.svg').default
import { Fragment } from 'react'


const statusObj = [
  '',
  'light-primary',
  'light-warning',
  'light-success',
  'light-secondary',
  'light-secondary',
  'light-ocean',
  'light-info',
  'light-danger',
  'light-primary'
]

const statusOptions = ['', 'Pending', 'In progress', 'Completed', 'On Hold', 'Cancelled', 'Sent to Review', 'Request Changes', 'To DO']

const dateFormat = (value) => {
  if (value?.length === 13) {
    return moment(value, 'x').format("MMM DD, YYYY")
  } else {
    return moment.unix(value).format("MMM DD, YYYY")
  }
}

const stringTrimfn = (text) => {
  return text.length >= 20 ? `${text.substr(0, 20)}...` : text
}

export const columns = [
  {
    name: 'ID',
    sortable: true,
    minWidth: '38px',
    sortField: 'uniqueidentity',
    selector: row => row.uniqueidentity,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        <div className='d-flex flex-column'>
          {row.uniqueidentity}
        </div>
      </div>
    )
  },
  {
    name: 'Client',
    sortable: true,
    minWidth: '202px',
    sortField: 'clientname',
    selector: row => row.clientname,
    cell: row => <Link to={`/task/view/${row.id}`}>
      <div className='d-flex justify-content-left align-items-center text-body'>
        <span className='text-capitalize'>
          {stringTrimfn(row.clientname)}</span></div>
    </Link>
  },
  {
    name: 'Service',
    minWidth: '202px',
    sortable: true,
    sortField: 'servicename',
    selector: row => row.servicename,
    cell: row => <Link to={`/task/view/${row.id}`}>
      <div className='d-flex justify-content-left align-items-center text-body'>
        <span className='text-capitalize d-flex'>
          {row.recurringtask && <img src={repeatsvg} className='width-10-per' />}
          <p className='mb-0 ms-25'>{row.servicename}</p>
        </span></div>
    </Link>
  },
  {
    name: 'Status',
    minWidth: '48px',
    sortable: false,
    sortField: 'taskstatus',
    selector: row => row.taskstatus,
    cell: row => (<Link to={`/task/view/${row.id}`}><div className='d-flex justify-content-left align-items-center'>

      <Badge className='text-capitalize' color={statusObj[parseInt(row.taskstatus)]} pill>
        {statusOptions[row.taskstatus]}
      </Badge>
    </div>
    </Link>
    )
  },
  {
    name: 'End Date',
    minWidth: '48px',
    sortable: true,
    sortField: 'enddate',
    cell: row => (
      <Link to={`/task/view/${row.id}`}>
        <div className='d-flex justify-content-left align-items-center'>
          <span>
            {row.enddate !== null ? dateFormat(row.enddate) : ''}
          </span></div>
      </Link>
    )
  },
  {
    name: 'Users',
    minWidth: '48px',
    sortable: true,
    sortField: 'assigeduser',
    cell: row => (
      <Fragment>
        {row.assigeduser && (
          row.assigeduser.map((obj, key) => { return (<Badge color='light-primary' key={key}>{JSON.parse(obj)?.name?.split(' ')[0]}</Badge>) })
        )}
      </Fragment>
    )
  }
]
